(function() {
  'use strict';

  angular
    .module('angular')
    .directive('acmeNavbar', acmeNavbar);

  /** @ngInject */
  function acmeNavbar() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/navbar/navbar.html',
      scope: {
          creationDate: '='
      },
      controller: NavbarController,
      controllerAs: 'vm',
      bindToController: true
    };

    return directive;

    /** @ngInject */
    function NavbarController($scope, $timeout, $mdSidenav, $log, $mdDialog, $rootScope, Catalog, APP_INFO, Website, Mail) {
      var vm = this;

      function init(){

        //Initialize variables
        vm.companyId = APP_INFO.ID;
        
        //Get Catalog collection titles
        Catalog.getCollections(APP_INFO.ID).then(function (data) { vm.collections = data; },function(err){ Mail.errorLog(err) });

        //Get navbar tabs
        Website.navbar().then(function(results){ vm.navbar = results; },function(err){ Mail.errorLog(err) });

        //Get total cart items
        vm.items = $rootScope.cart.getTotalItems();

      }

      init();

      $rootScope.$on('updateItemQ', function (event, data) {
        vm.items = $rootScope.cart.getTotalItems();
      });

      $scope.toggleLeft = buildDelayedToggler('left');
      $scope.toggleRight = buildToggler('right');
      $scope.isOpenRight = function(){
        return $mdSidenav('right').isOpen();
      };
      /**
       * Supplies a function that will continue to operate until the
       * time is up.
       */
      function debounce(func, wait, context) {
        var timer;
        return function debounced() {
          var context = $scope,
              args = Array.prototype.slice.call(arguments);
              $timeout.cancel(timer);
              timer = $timeout(function() {
                timer = undefined;
                func.apply(context, args);
              }, wait || 10);
            };
      }
      /**
       * Build handler to open/close a SideNav; when animation finishes
       * report completion in console
       */
      function buildDelayedToggler(navID) {
        return debounce(function() {
          $mdSidenav(navID)
            .toggle()
            .then(function () {
              $log.debug("toggle " + navID + " is done");
            });
        }, 200);
      }
      function buildToggler(navID) {
        return function() {
          $mdSidenav(navID)
            .toggle()
            .then(function () {
              $log.debug("toggle " + navID + " is done");
            });
        }
      }

      var originatorEv;
      $scope.openMenu = function($mdOpenMenu, ev) {
        originatorEv = ev;
        $mdOpenMenu(ev);
      };
      this.notificationsEnabled = true;
      this.toggleNotifications = function() {
        this.notificationsEnabled = !this.notificationsEnabled;
      };
      this.redial = function() {
        $mdDialog.show(
          $mdDialog.alert()
            .targetEvent(originatorEv)
            .clickOutsideToClose(true)
            .parent('body')
            .title('Suddenly, a redial')
            .textContent('You just called a friend; who told you the most amazing story. Have a cookie!')
            .ok('That was easy')
        );
        originatorEv = null;
      };
    }
  }

})();

(function() {
  'use strict';

  angular
    .module('angular')
    .factory('githubContributor', githubContributor);

  /** @ngInject */
  function githubContributor($log, $http) {
    var apiHost = 'https://api.github.com/repos/Swiip/generator-gulp-angular';

    var service = {
      apiHost: apiHost,
      getContributors: getContributors
    };

    return service;

    function getContributors(limit) {
      if (!limit) {
        limit = 30;
      }

      return $http.get(apiHost + '/contributors?per_page=' + limit)
        .then(getContributorsComplete)
        .catch(getContributorsFailed);

      function getContributorsComplete(response) {
        return response.data;
      }

      function getContributorsFailed(error) {
        $log.error('XHR Failed for getContributors.\n' + angular.toJson(error.data, true));
      }
    }
  }
})();

(function() {
  'use strict';

  angular
    .module('angular')
    .directive('acmeMalarkey', acmeMalarkey);

  /** @ngInject */
  function acmeMalarkey(malarkey) {
    var directive = {
      restrict: 'E',
      scope: {
        extraValues: '='
      },
      template: '&nbsp;',
      link: linkFunc,
      controller: MalarkeyController,
      controllerAs: 'vm'
    };

    return directive;

    function linkFunc(scope, el, attr, vm) {
      var watcher;
      var typist = malarkey(el[0], {
        typeSpeed: 40,
        deleteSpeed: 40,
        pauseDelay: 800,
        loop: true,
        postfix: ' '
      });

      el.addClass('acme-malarkey');

      angular.forEach(scope.extraValues, function(value) {
        typist.type(value).pause().delete();
      });

      watcher = scope.$watch('vm.contributors', function() {
        angular.forEach(vm.contributors, function(contributor) {
          typist.type(contributor.login).pause().delete();
        });
      });

      scope.$on('$destroy', function () {
        watcher();
      });
    }

    /** @ngInject */
    function MalarkeyController($log, githubContributor) {
      var vm = this;

      vm.contributors = [];

      activate();

      function activate() {
        return getContributors().then(function() {
          $log.info('Activated Contributors View');
        });
      }

      function getContributors() {
        return githubContributor.getContributors(10).then(function(data) {
          vm.contributors = data;

          return vm.contributors;
        });
      }
    }

  }

})();

(function() {
    'use strict';

    angular
        .module('angular')
        .directive('acmeFooter', acmeFooter);

    /** @ngInject */
    function acmeFooter() {
        var directive = {
            restrict: 'E',
            templateUrl: 'app/components/footer/footer.html',
            scope: {
                creationDate: '='
            },
            controller: FooterController,
            controllerAs: 'vm',
            bindToController: true
        };

        return directive;

        /** @ngInject */
        function FooterController(Website, APP_INFO, Mail) {
            var vm = this;
            vm.companyId = APP_INFO.ID;

            Website.footer().then(function(results){
              vm.footer = results;
              var logo_url = "//cms.blumewebsites.com/" + vm.footer.logoUrl;

              if(APP_INFO.chat.facebook){
                //Initialize Chat Bubble
                  var options = {
                        facebook: APP_INFO.chat.facebook, // Facebook page ID
                        whatsapp: APP_INFO.chat.whatsapp, // WhatsApp number
                        company_logo_url: logo_url, // URL of company logo (png, jpg, gif)
                        greeting_message: APP_INFO.chat.longMessage, // Text of greeting message
                        call_to_action: APP_INFO.chat.shortMessage, // Call to action
                        button_color: "#129BF4", // Color of button
                        position: "right", // Position may be 'right' or 'left'
                        order: APP_INFO.chat.order // Order of buttons
                    };
                    var proto = document.location.protocol, host = "whatshelp.io", url = proto + "//static." + host;
                    var s = document.createElement('script'); s.type = 'text/javascript'; s.async = true; s.src = url + '/widget-send-button/js/init.js';
                    s.onload = function () { WhWidgetSendButton.init(host, proto, options); };
                    var x = document.getElementsByTagName('script')[0]; x.parentNode.insertBefore(s, x);
              }

            },function(err){ Mail.errorLog(err) })
            vm.selectedMode = 'md-fling';
            vm.selectedDirection = 'right';
            vm.isOpen = false;

        };

    }

})();

(function(){
  angular
    .module('angular')
    .controller('ContactController',ContactController);
    function ContactController($scope, $http){
	      
	      init();

	   function init(){

		   //Initialize Variables
		   $scope.delivered = false;
		   $scope.loading = false;
		   $scope.contact = {
		   		name: "",
		   		email: "",
		   		message: ""
		   };
		   //Bind functions
		   $scope.sendMessage = sendMessage;
	   }

	   function sendMessage(){
	   		$scope.loading = true;
	   		var company_email = "info@golden-pet.com";
	   		var company_name = "Golden Pet";
	   		var customer_email = $scope.contact.email;
	   		var customer_name = $scope.contact.name;

	   		//build message
	   		var message = "Correo: " + $scope.contact.email + "\n\n"
   						+ "Nombre: " + $scope.contact.name + "\n\n"
						+ "Mensaje: " + $scope.contact.message + "\n\n";

	   		$http({
                  method: "POST",
                  url: "https://central-api.madebyblume.com/v1/mail/contact",
                  params: {
                      company_email: company_email,
                      company_name: company_name,
                      customer_email: customer_email,
                      customer_name: customer_name,
                      message: message
                  },
                  headers: {
                      'Accept': 'application/json',
                      'Content-Type': 'application/json'
                  }
              }).then(function (response) {
              		$scope.loading = false;
              		$scope.delivered = true;
                  })
	   }

    }
})();

(function() {
    'use strict';

    angular
        .module('angular')
        .controller('CatalogController', CatalogController);
        function CatalogController($scope, Catalog, Products, APP_INFO, Personalization, Mail, $stateParams, $state) {
        var vm = this;

        $scope.setCategoryFilter = setCategoryFilter;
        init();

        function init() {

          $scope.selectedCategory = "";
          if($stateParams.productType){
            $scope.selectedCategory = $stateParams.productType;
          }
          $scope.catalog = [];
          vm.load_count = 0;
          vm.busy = false;
          vm.styles = Personalization.styles;
          // vm.stop_loading = false;
          vm.itemsDisplayed = 8;
          vm.addMoreItems = addMoreItems;
          vm.companyId = APP_INFO.ID;
          vm.setSizeFilter = setSizeFilter;

          vm.loader = true;
          if($stateParams.variantName){
            Products.allByVariantName($stateParams.variantName)
              .then(function (data) {
                  vm.catalog = data;
                  vm.loader = false;
              },function(err){ Mail.errorLog(err) });
          } else {
            Products.all()
              .then(function (data) {
                  vm.catalog = data;
                  vm.loader = false;
              },function(err){ Mail.errorLog(err) });
          }

          Catalog.getCategories(APP_INFO.ID)
              .then(function (data) {
                  $scope.categories = data;
              },function(err){ Mail.errorLog(err) });

          //Set store configuration
          Catalog.setStoreData(APP_INFO.ID)
            .then(function (response) {
              vm.currency = response.data.currency;
            },function(err){ Mail.errorLog(err) });

        }

        function setSizeFilter(sizeFilter){
            if(sizeFilter != ''){
                $state.go('productVariantsByName' ,{variantName: sizeFilter});
            } else {
                $state.go('products');
            }
        }

        function addMoreItems(){
            vm.itemsDisplayed += 4;
        }

        function setCategoryFilter(){
          console.log($scope.selectedCategory);
            $state.go('productsAlt',{'productType':$scope.selectedCategory});

        }
    }
})();

(function() {
  'use strict';

  angular
    .module('angular')
    .controller('ProductController', ProductController);

  /** @ngInject */
  function ProductController(Catalog,APP_INFO,$scope,$stateParams, Mail) {
    var vm = this;
    $scope.active = 0;
    init();
    ////////////////

    function init() {

      vm.option1 = "";
      vm.option2 = "";
      vm.option3 = "";
      vm.outOfStock = [];

      var product_id = $stateParams.product_id;

      //Set store configuration
      Catalog.setStoreData(APP_INFO.ID)
        .then(function (response) {
          $scope.currency = response.data.currency;
        },function(err){ Mail.errorLog(err) });

      Catalog.productExpand(product_id)
          .then(function (data) {
              $scope.product = data;

              for (var i = $scope.product.variants.length - 1; i >= 0; i--) {
                $scope.product.variants[i].name = $scope.product.variants[i].name.trim();
                if($scope.product.variants[i].quantity == 0){
                  vm.outOfStock.push($scope.product.variants[i].name);
                  $scope.product.variants.splice(i, 1);
                }
              };

              $scope.price = $scope.product.variants[0].price;

              if($scope.product.options.length > 0){
                  vm.option1 = $scope.product.options[0].values[0];
              }
              if($scope.product.options.length > 1){
                  vm.option2 = $scope.product.options[1].values[0];
              }
              if($scope.product.options.length > 2){
                  vm.option3 = $scope.product.options[2].values[0];
              }

              if(vm.option2 == "" && vm.option3 == ""){
                for (var k = 0; k < vm.outOfStock.length; k++) {
                  for (var j = 0; j < $scope.product.options[0].values.length; j++) {
                    if(vm.outOfStock[k] == $scope.product.options[0].values[j]){
                      $scope.product.options[0].values.splice(j,1);
                    }
                  };
                };
              }

          },function(err){ Mail.errorLog(err) })

    }

    $scope.setActive = function(index) {
        $scope.product.images[index].active=true;
      }

    $scope.share = function(product){
      window.open('https://www.facebook.com/sharer.php?caption=' + product.title + '&u=https://cms.blumewebsites.com/' + product.images[0].relative_url, 'Share', 'scrollbars=yes,resizable=yes,toolbar=no,menubar=no,scrollbars=no,location=no,directories=no,width=400, height=300, top=300, left=300' );
    }
  }
})();

