    AnalyticsProvider.setAccount('UA-62919114-3');
    AnalyticsProvider.trackPages(true);
    AnalyticsProvider.trackUrlParams(true);
    AnalyticsProvider.setPageEvent('$stateChangeSuccess');
    // Enable log
